@import '_variables';
.navBar .logo img {
  float: left;
  max-height: 120px;
}
.is-active {color: $active;}
.navBar .col-md-3 {
  z-index: 7;
}
ul.main-menu {
  display: inline-flex;
  list-style: none;
  margin: 0px auto 0;
  padding: 0;
  float: right;
  z-index: 7;
}
ul.main-menu>li.menu-item:first-child {
  padding-left: 0;
  margin-left: 0;
}
.menu-item {
  line-height: 23px;
  display: flex;
  flex-direction: row;
  margin: 20px 7px;
}
li {
  list-style-type: none;
}
.menu-item a {
  font-size: 18px;
  font-weight: 500;
  line-height: 40px;
  position: relative;
  color: $textColor;
  text-decoration: none;
  cursor: pointer;
}
.svg__logins svg{
  font-size: 25px;
  vertical-align: middle;
  margin-top: 4px;
  margin-right: 3px;
  color: $textColor;
}
.header__middle__logo{
  width: 20%;
  display: inline-block;
}
.header__middle__menus {
  width: 70%;
  display: inline-block;
}
.menu-item.menu__qqqitem a{
  margin: 27px 10px;
}
.header__middle__search {
  width: 7%;
  display: inline-block;
  padding: 36px 3px 27px 3px;
  border-left: 1px solid $lavender;
}
.header__middle {
  display: flex;
  width: 100%;
  float: left;
  border-bottom: 1px dashed $lavender;
  position: relative;
}
.header__middle__logo > a > h1 {
  text-transform: uppercase;
  text-decoration: none;
  margin-top: 33px;
}
.active{
  color: $active;
}
.header__middle__menus span.text {
  position: absolute;
  right: 177px;
  bottom: 10px;
  font-size: 14px;
  color: #ff1800;
}
.sub__menus__arrows{
  position: relative;
}
.sub__menus__arrows svg, .sub__menus__arrows__full svg{
  padding-top: 0px;
  position: absolute;
  top: 13px;
}
.sub__menus {
  position: absolute;
  display: none;
  background: $white;
  border: 1px solid $lavender;
  width: 227px;
  left: -17px;
  padding: 2px 0 0 0;
  z-index: 1000;
  box-shadow: $active;
}

@media (max-width:767px) { 
  .header__middle .ConfirmEmail__notifications {top: 187%;}
  .header__middle .ConfirmEmail__notifications h1 {font-size: 14px;font-weight: 400;}
  .header__middle .ConfirmEmail__notifications {padding: 7px 7px 7px 7px;top: 77%;}
  .header__middle .active {color:$active}
}

@media (min-width:992px) { 
  .sub__menus__arrows:hover .sub__menus{
    display: block;
  }
  .sub__menus__arrows__full:hover .sub__menus{
    display: block;
    z-index: 1000000;
  }
}
